.skill-container {
  width: 100%;
  padding: 3rem 1rem;
  background-color: #000;
}

.card {
  border: 1px solid #eee;
  color: #eee;
  text-align: center;
  padding: 1rem;
}

.card:hover {
  background-color: #141414;
  cursor: default;
}

.card h3 {
  font-size: 1.4rem;
  padding: 1rem;
}

.card p {
  padding: 10px 0;
}

.bar {
  border-bottom: 1px solid #eee;
  width: 10%;
  margin: 1.5rem auto;
  display: block;
}

.btc {
  font-size: 4rem;
  font-weight: 600;
  padding: 1rem 0;
}

.card .btn {
  display: block;
  width: 80%;
  margin: 2rem auto;
}

.project-title {
  color: #fff;
  padding: 1rem 0;
}

.project-details p {
  padding-bottom: 1rem;
  font-size: 1.1rem;
  text-align: justify;
}

.project-btns {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
}

.project-btns .btn {
  padding: 0.5rem 1rem;
}

@media screen and (max-width: 740px) {
  .skill-container {
    max-width: 90%;
    margin: auto;
    grid-template-columns: 1fr;
  }
}
